.bank-card-list {
  padding: 0.266667rem;
}

.template-agreement {
  display: none;
}

.account-agreement h3 {
  font-size: 14px;
  text-align: center;
}

[data-dpr="1"] .account-agreement h3 {
  font-size: 14px;
}

[data-dpr="2"] .account-agreement h3 {
  font-size: 28px;
}

[data-dpr="3"] .account-agreement h3 {
  font-size: 42px;
}

.account-agreement p {
  padding: 0.133333rem 0;
  text-indent: 0.6rem;
  font-size: 12px;
}

[data-dpr="1"] .account-agreement p {
  font-size: 12px;
}

[data-dpr="2"] .account-agreement p {
  font-size: 24px;
}

[data-dpr="3"] .account-agreement p {
  font-size: 36px;
}


