.fun-block {
  position: relative;
  padding: 0.16rem;
  background-color: #fff;
}

.fun-block:before {
  content: '';
  position: absolute;
  z-index: 1;
  height: 1px;
  top: 0;
  left: 0;
  right: 0;
  background-color: #d9d9d9;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
  -webkit-transform: scaleY(0.5);
  -webkit-transform-origin: 0 0 50%;
  transform-origin: 0 0 50%;
  -webkit-transform-origin: 0 0;
}

.fun-block:after {
  content: '';
  position: absolute;
  z-index: 1;
  height: 1px;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #d9d9d9;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
  -webkit-transform: scaleY(0.5);
  -webkit-transform-origin: 100% 100% 50%;
  transform-origin: 100% 100% 50%;
  -webkit-transform-origin: 100% 100%;
}

.fun-block.fixed {
  background-color: #DFDFDF;
}

.fun-block.fixed .list .item .fun {
  background-color: #DFDFDF;
}

.fun-block .bnt-editor {
  position: absolute;
  top: 0.2rem;
  right: 0.266667rem;
}

.fun-block h2 {
  line-height: 0.533333rem;
  color: #777;
  font-size: 12px;
}

[data-dpr="1"] .fun-block h2 {
  font-size: 12px;
}

[data-dpr="2"] .fun-block h2 {
  font-size: 24px;
}

[data-dpr="3"] .fun-block h2 {
  font-size: 36px;
}

.fun-block .list {
  padding: 0.133333rem 0.4rem 0.266667rem 0.4rem;
  overflow: hidden;
  *zoom: 1;
}

.fun-block .list .item {
  float: left;
  margin-top: 0.133333rem;
  padding: 0.066667rem 0.266667rem;
  width: 25%;
}

.fun-block .list .item .fun {
  position: relative;
  padding: 0.133333rem 0;
  width: 100%;
  text-align: center;
  border: dotted 1px rgba(51, 51, 51, 0);
}

.fun-block .list .item .fun .icon {
  display: inline-block;
  width: 1.066667rem;
  height: 1.066667rem;
  background-image: url("../../assets/img/icon-bank-logo.png");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center center;
  border: solid 1px #d9d9d9;
}

.fun-block .list .item .fun cite {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  -ms-text-overflow: ellipsis;
  text-overflow: ellipsis;
}

.fun-block .list .item .fun .btn-add,
.fun-block .list .item .fun .btn-delete {
  display: none;
  position: absolute;
  top: -0.133333rem;
  right: 0.2rem;
  width: 0.666667rem;
  height: 0.666667rem;
}

.fun-block .list .item .fun .btn-add:before,
.fun-block .list .item .fun .btn-delete:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0.533333rem;
  height: 0.533333rem;
  background-image: url("../../assets/img/shortcut-editor/icon-add.png");
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center center;
}

.fun-block .list .item .fun .btn-delete:before {
  width: 0.48rem;
  height: 0.48rem;
  background-image: url("../../assets/img/shortcut-editor/icon-delete.png");
}

.fun-block .list.dragging .item .fun {
  border: dotted 1px rgba(51, 51, 51, 0.4);
}

.fun-block .list.dragging .item[draggable="true"] .fun {
  border: dotted 1px rgba(255, 0, 0, 0.6);
}

.fun-block .list.dragging .item.sortable-drag[draggable="true"] .fun {
  border: dotted 1px rgba(51, 51, 51, 0.4);
}

.fun-block .wran {
  padding: 0.066667rem 0;
  font-size: 10px;
  text-align: center;
  color: #999;
  line-height: 1.4em;
}

[data-dpr="1"] .fun-block .wran {
  font-size: 10px;
}

[data-dpr="2"] .fun-block .wran {
  font-size: 20px;
}

[data-dpr="3"] .fun-block .wran {
  font-size: 30px;
}

.fun-block + .fun-block {
  margin-top: 0.213333rem;
}

.shortcut-editor.show-editor .fun-block .list .item .btn-add,
.shortcut-editor.show-editor .fun-block .list .item .btn-delete {
  display: block;
}

.draggable-move {
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}


